#Footer {
    width: 100%;
    height: 10vh;
    background-color: var(--header-bg-color);
    color: var(--main-fg-color);
}

.Footer__container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
}

.Footer__left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 1em;
}

.Footer__left p, a{
    padding: 2px 0;
    font-size: 14px;
}

.Footer__left a, .Footer__right a {
    color: var(--main-fg-color);
    text-decoration: none;
    transition: 200ms ease;
}

.Footer__right {
    display: flex;
    align-items: center;
    font-size: 40px;
    margin: 0 1em;
}

.Footer__container i {
    font-size: 40px;
    padding: 0 5px;
    transition: 200ms ease;
}

.Footer__right i:hover, .Footer__left a:hover {
    color: var(--hover-color);
}

@media only screen and (max-width: 950px) {
    #Footer {
        height: fit-content;
    }

    .Footer__container {
        flex-direction: column-reverse;
    }

    .Footer__right {
        margin: 0.5em 0;
        display: flex;
        justify-content: center;
    }

    .Footer__left {
        margin-bottom: 0.5em;
    }
}